	data() {
		return {
			couponValidList: [],
			couponinvalidList: [],
			headerTop: 0,
			//控制滑动效果
			typeClass: 'valid',
			subState: ''
		};
	},
	onPageScroll(e) {},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onLoad(option) {
		// console.log(option);
		// this.goodsPrice = option.goodsPrice;
		uni.request({
			url: this.$tempUrl + 'buyer/coupons/listForOrder',
			method: 'GET',
			data: {
				amount: parseInt(option.goodsPrice),
				openId: uni.getStorageSync('openId'),
				shopId: uni.getStorageSync('shopId')
			},
			success: res => {
				// console.log(res.data.data);
				const data = res.data.data;
				this.couponValidList = data.unable;
				this.couponinvalidList = data.disable;
				// console.log(data.unable);
				// console.log(this.couponValidList);
				// console.log(this.couponinvalidList);
			}
		});
		//兼容H5下排序栏位置










	},
	methods: {
		selected(row) {
			uni.setStorage({
				key: 'confirmCoupon',
				data: row,
				success: () => {
					uni.navigateBack({});
				}
			});
		},
		switchType(type) {
			if (this.typeClass == type) {
				return;
			}
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 0
			});
			this.typeClass = type;
			this.subState = this.typeClass == '' ? '' : 'show' + type;
			setTimeout(() => {
				// this.oldIndex = null;
				// this.theIndex = null;
				this.subState = this.typeClass == 'valid' ? '' : this.subState;
			}, 200);
		}
	}
};
